  padding: 10px; }

.btn {
  height: 60px;
  width: 60px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  margin: 2rem 0;
  color: #ffffff;
  background-image: -webkit-linear-gradient(left, #18d5fd, #2065f0);
  background-image: linear-gradient(to right, #18d5fd, #2065f0);
  border-radius: 50%;
  border: none;
  box-shadow: 0 3px 4px 0px #66666663; }

.btn:hover {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  box-shadow: 0 1px 4px 0px #66666663; }

.btn-clicked, .btn-copied {
  -webkit-animation: BtnClicked .4s ease forwards;
          animation: BtnClicked .4s ease forwards; }

@-webkit-keyframes BtnClicked {
  0% {
    box-shadow: 0 1px 4px 0px rgba(100, 100, 150, 0.2); }
  100% {
    box-shadow: 0 1px 4px 30px rgba(100, 100, 150, 0); } }

@keyframes BtnClicked {
  0% {
    box-shadow: 0 1px 4px 0px rgba(100, 100, 150, 0.2); }
  100% {
    box-shadow: 0 1px 4px 30px rgba(100, 100, 150, 0); } }

.icon-ok {
  color: #2AB812; }

a:link, a:visited, a:active {
  color: #ffa015;
  text-decoration: none; }

.yandex-span {
  padding: .5rem;
  align-self: center;
  text-align: center;
  font-size: .7rem; }

.flag {
  height: 20px;
  width: 40px; }

.flag__container {
  position: relative;
  height: 27px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f7; }

.flag__overflow {
  position: relative;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center; }

.active-language {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: transparent;
  background-image: -webkit-linear-gradient(45deg, transparent 50%, gray 50%), -webkit-linear-gradient(315deg, gray 50%, transparent 50%), -webkit-linear-gradient(left, #ccc, #ccc);
  background-image: linear-gradient(45deg, transparent 50%, gray 50%), linear-gradient(135deg, gray 50%, transparent 50%), linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
  background-repeat: no-repeat;
  outline: none;
  width: auto;
  height: 27px;
  border: none;
  background-color: #f5f5f7;
  padding: 5px 0;
  font-weight: bold;
  text-transform: capitalize;
  font-size: 12px;
  line-height: 14px; }

.middle {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 201;
  color: #d7d7de;
  box-shadow: 5px 0 0 0 #f3f3f4;
  height: 50px;
  width: 50px !important;
  background-color: #ffffff;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(90deg);
          transform: translate(-50%, -50%) rotate(90deg);
  padding: 10px; }

@media (max-width: 400px) {
  .middle {
    display: none; } }

.copy-clipboard {
  height: 30px;
  width: 30px;
  line-height: 20px;
  background-color: #ffffff;
  right: 0;
  bottom: 0;
  margin: 0 10px 10px 0;
  border: 0;
  color: #d7d7de;
  border-radius: 50%;
  box-shadow: 0 5px 0 0 #f3f3f4;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute; }

/* loader  */
/* From https://loading.io/css/ */

.lds-spinner {
  max-width: 64px;
  max-height: 64px;
  width: 22px;
  height: 22px;
  -webkit-transform: translate(-11px, -18px);
          transform: translate(-11px, -18px);
}

.lds-spinner div {
  -webkit-transform-origin: 21px 28px;
          transform-origin: 21px 28px;
  -webkit-animation: lds-spinner 1.2s linear infinite;
          animation: lds-spinner 1.2s linear infinite;
}

.lds-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 17px;
  left: 20px;
  width: 2px;
  height: 8px;
  border-radius: 20%;
  background: silver;
}
.lds-spinner div:nth-child(1) {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation-delay: -1.1s;
          animation-delay: -1.1s;
}
.lds-spinner div:nth-child(2) {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
.lds-spinner div:nth-child(3) {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
}
.lds-spinner div:nth-child(4) {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}
.lds-spinner div:nth-child(5) {
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
}
.lds-spinner div:nth-child(6) {
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
  -webkit-animation-delay: -0.6s;
          animation-delay: -0.6s;
}
.lds-spinner div:nth-child(7) {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
.lds-spinner div:nth-child(8) {
  -webkit-transform: rotate(210deg);
          transform: rotate(210deg);
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}
.lds-spinner div:nth-child(9) {
  -webkit-transform: rotate(240deg);
          transform: rotate(240deg);
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-spinner div:nth-child(10) {
  -webkit-transform: rotate(270deg);
          transform: rotate(270deg);
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
}
.lds-spinner div:nth-child(11) {
  -webkit-transform: rotate(300deg);
          transform: rotate(300deg);
  -webkit-animation-delay: -0.1s;
          animation-delay: -0.1s;
}
.lds-spinner div:nth-child(12) {
  -webkit-transform: rotate(330deg);
          transform: rotate(330deg);
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
@-webkit-keyframes lds-spinner {
  0% {
    opacity: .8;
  }
  100% {
    opacity: 0;
  }
}
@keyframes lds-spinner {
  0% {
    opacity: .8;
  }
  100% {
    opacity: 0;
  }
}

