      -webkit-transform: translateY(-50px);
              transform: translateY(-50px); }

@-webkit-keyframes slideLeft {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  100% {
    -webkit-transform: translateX(-110vw);
            transform: translateX(-110vw); } }

